import common from'../../util/common.js'
import code from "../../util/code.js"
import qs from"../../components/qs.js"
export default {
	data() {
		return {
			tel:'',//手机号
			pwd:'',//密码
		}
	},
	onLoad() {
		// uni.setStorageSync("pageState","2");
	},
	methods: {
		register:function(){
			uni.redirectTo({
				url: '../register/register'
			})
		},
		//登录
		login:function(){
			var telV = code.validatePhoneNumber(this.tel);
			console.log(telV);
			if(telV == false){
				uni.showToast({
					title: "请输入正确手机号",
					icon: 'none',
					duration: 2000
				})
				return
			}else if(this.pwd == ""){
				uni.showToast({
					title: "请输入密码",
					icon: 'none',
					duration: 2000
				})
				return
			}else{
				uni.showLoading({
					title: '加载中...',
				})
				uni.request({
					url: common.port.mLogin,
					header: {
						'content-type': 'application/x-www-form-urlencoded',
					},
					method: "POST",
					dataType: "json",
					data:{
						phone: this.tel,
						password: this.pwd,
					},
					success: (result) => {
						uni.hideLoading();
						console.log(result.data);
						if (result.data.code == 500) {
							uni.showToast({
								title: result.data.msg,
								icon: 'none',
								duration: 2000
							})
							return
						} else {
							//登录成功
							uni.setStorageSync('userIdS', result.data.id);
							uni.setStorageSync('loginStateS', "1");
							//成功并且判断一下是否存在成绩
							var examidV = uni.getStorageSync("examIdS");
							var userId = uni.getStorageSync("userIdS");
							uni.request({
								url: common.port.lastLog,
								header: {
									'content-type': 'application/x-www-form-urlencoded',
								},
								method: "POST",
								dataType: "json",
								data: {
									exam_id: examidV,
									user_id: userId
								},
								success: (result) => {
									//存在分数
									if(result.score>0){
										uni.navigateTo({
											url:"../grade/grade"
										})
									}
									else{
										// 不存在分数
										var authorityV = uni.getStorageSync("authorityS");
										var pwdV = uni.getStorageSync("pwdstartS");
										if(authorityV == 2){//密码答题
											if(pwdV == 1 ){//表示密码登录过了
												uni.navigateTo({
													url:"../notice/notice"
												})
											}else{
												uni.navigateTo({
													url:"../pwd/pwd"
												})
											}
										}else{
											uni.navigateTo({
												url:"../notice/notice"
											})
										}
									}
								}
									
							})
						}
					}
					
				})
			}
		}
	}
}
